.results {
  position: relative;
  letter-spacing: 0.26px;
}
.flightCards {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 12px;
  text-align: left;
  font-size: 18px;
  color: #7e8b97;
  font-family: Roboto;
}
@media screen and (max-width: 1100px) {
  .flightCards {
    flex: unset;
    align-self: stretch;
  }
}
