.primaryButtonChild {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: 6px;
  background-color: #f99a0e;
}
.searchFlights {
  position: absolute;
  top: calc(50% - 10.5px);
  left: calc(50% - 62.5px);
  letter-spacing: 0.04em;
  font-weight: 500;
}
.primaryButton {
  position: relative;
  width: 220px;
  height: 72px;
  text-align: center;
  font-size: 18px;
  color: #fff;
  font-family: Roboto;
}
